.navbar {
    background-color: black;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
}

.navbar-container {
    min-height: 66px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 1200px;
    padding: 0px 10px;
    margin: auto;
    font-weight: bold;
    color: white;
}

