.cvhead {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 90px 0 0 0;
    justify-content: space-around;
}

.cv-contact {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.cv-contact-list {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    max-width: 500px;
    .icon {
        position: relative;
        top: 6px;
    }
}