@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body{margin:0;font-family:"Lato";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html{scroll-behavior:smooth}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.contact-head h4{font-weight:500;font-size:1.5rem;text-transform:uppercase;text-align:center;margin-top:0}.contact-head p{text-align:center;color:rgba(0,0,0,.5)}.contact{background-color:rgba(248,249,255,.9);padding:80px 10%}.contact-item{display:flex;flex-direction:column;text-align:center}.contact-item p{margin:0}
.footer{padding:20px;background-color:#000;text-align:center;color:#fff}
.profilepic{height:200px;border:3px solid rgba(255,255,255,.5);border-radius:50rem;margin:auto auto 0px auto}.profilepiclabel{font-weight:500;font-size:2.5rem;margin:.5rem auto auto auto}
.hero{background:url(/static/media/nebula.e1656dd3.png) center center no-repeat;background-size:cover;height:100vh;display:flex;flex-direction:column;justify-content:space-around}.scroll{position:absolute;bottom:30px;left:0;right:0;border:0;margin:auto;height:50px}
.socitem {
    list-style: none;
}

.soclink{
    color: white;
    text-decoration: none;
    font-size: 1.8rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.soclink:hover{
    color: rgba(255, 255, 255, 0.75);
}
.socials{display:none}.socials-mobile{display:inline;display:initial;margin-top:.5rem}.socials-items{display:flex;flex-direction:row;justify-content:center;padding:0}@media(min-width: 800px){.socials{display:inline;display:initial}}
.logo{display:flex;flex-direction:column;justify-content:center;color:#fff;text-decoration:none;font-size:1.25rem}
.anchor:target {padding-top: 250px;}

.navitem{
    list-style: none;
}

.navlink{
    color: rgba(255, 255, 255, 0.5);
    text-decoration: none;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.navlink:hover{
    color: rgba(255, 255, 255, 0.75);
}
.hamburger-react{margin-right:5px;margin-left:auto}.nav-desktop{display:none}.nav-mobile{background:rgba(0,0,0,.75);text-align:center;position:fixed;right:0px;top:10px}.nav{display:flex;flex-direction:column;justify-content:center}.nav-items{display:flex;flex-direction:column;padding:0}@media(min-width: 800px){.nav-mobile{display:none}.nav-desktop{display:inline;display:initial}.nav-items{flex-direction:row}}
.navbar{background-color:#000;width:100%;position:fixed;top:0;left:0;right:0;z-index:100}.navbar-container{min-height:66px;display:flex;flex-direction:row;justify-content:space-between;max-width:1200px;padding:0px 10px;margin:auto;font-weight:bold;color:#fff}
.profile{flex-direction:column;padding:20px}.profile .solverimg img{width:100%}.profile .solvername{font-weight:700;font-size:1.5rem;margin:0}.profile .solverlabel{font-weight:700;font-size:1rem;margin-top:.8rem;margin-bottom:0}.profile .solver{display:flex;flex-direction:column;justify-content:center;padding:20px 50px 20px 50px}.profile .solvertext{font-weight:400;line-height:1.5;text-align:justify;color:rgba(0,0,0,.6)}.profile .solvercontact{list-style:none;line-height:1.5;padding:0}@media(min-width: 768px){.profile{max-width:1000px;padding:50px;margin:auto;display:flex;flex-direction:row}.profile .solverimg{max-width:30%;margin:auto}}
.button{font-family:"Lato";font-size:1rem;line-height:1.5;border-radius:.25rem;padding:.375rem .75rem;border:1px solid transparent;margin:.25rem;white-space:nowrap}.button:focus{outline:thin}.primarybtn{color:#fff;background:#008081;font-weight:400;cursor:pointer;transition:color .15s ease-in-out}.primarybtn:hover{color:rgba(0,0,0,.75)}.toggle{background-color:transparent;font-size:1.25rem;font-weight:400;cursor:pointer;border:#d3d3d3 solid 1px;transition:color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out}.toggle.active{color:#fff;background:#008081;border:0}
.projects-head h4{font-weight:500;font-size:1.5rem;text-transform:uppercase;text-align:center;margin-top:0}.projects-head p{text-align:center;color:rgba(0,0,0,.5)}mark{background-color:rgba(255,255,255,.7);padding:0 5px}.projects{display:flex;flex-direction:column;padding:80px 10%}.projects-gal{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}.projects-gal-item{margin:.5rem .5rem 0 .5rem;position:relative;transition:all 1s ease}.projects-gal-item:hover{transform:rotateY(15deg) perspective(1px)}.projects-gal-item img{box-shadow:10px 10px 5px #b4b4b4;width:100%}.projects-gal-item-pitch,.projects-gal-item-name{position:relative;top:-55px;left:10px}.projects-gal-item-name{font-size:1.3rem;font-weight:500}.projects-gal-item-pitch{font-size:1rem;font-weight:300}
.details{padding:0px 24px 16px 24px}#dialog-title h2{display:flex;justify-content:space-between}.name{margin:auto 0}.pitch{font-weight:bold;margin:5px 0}.techimg{margin-right:5px}.techtools{margin-top:10px}Dialog .details .desc{margin-top:8px !important}
.skillsgraph{display:flex;flex-direction:column}.skillsgraph .skill-bar{height:4px;border:1px solid #d3d3d3;border-radius:3px}.skillsgraph .skill-item{padding:10px 0}.skillsgraph .skill-label{position:absolute;top:-25px;right:-18px}.skillsgraph .skill-level{position:relative;height:2px;background-color:#008081;margin:1px 2px;-webkit-animation:animate-skill 2s;animation:animate-skill 2s}@-webkit-keyframes animate-skill{0%{width:0}}@keyframes animate-skill{0%{width:0}}.skillsgraph .skill-list{padding:20px 10px}.skillsgraph .skill-list ul{display:none;flex-direction:column;list-style:none;padding:0}.skillsgraph .skill-list ul.active{display:inline;display:initial}.skillsgraph .skill-name{position:relative;top:-5px}.skillsgraph .skill-switch{display:flex;flex-direction:column;flex-wrap:nowrap}@media(min-width: 768px){.skillsgraph .skillsgraph{flex-direction:row}.skillsgraph .skill-list{width:100%}.skillsgraph .skill-switch{margin-right:20px}}
.skillspanellabel h4{font-weight:500;font-size:1.5rem;text-transform:uppercase;text-align:center;margin-top:0}.skillspanellabel p{text-align:center;color:rgba(0,0,0,.5)}.section-skills{display:flex;position:relative;background-color:rgba(248,249,255,.9)}.skillsimg{background-image:url(/static/media/coding.3847b1e2.jpg);min-height:100%;width:100%;background-size:cover;background-position:center;position:absolute;z-index:-1}.skillspanel{height:100%;width:100%;padding:80px 10%}.skillspanellabel{padding-bottom:20px}@media(min-width: 992px){.skillsimg{width:33%;z-index:0}.skillspanel{margin-left:33%;padding:80px 5%}}
.experience h2{text-transform:uppercase;font-weight:300;margin:0}.experience ul{list-style:none;padding-left:15px}.experience .item{display:flex}.experience .item .period{min-width:160px;font-weight:bold}.experience .item .role,.experience .item .company{font-weight:bold}.experience .item p{margin:0}
.cvhead{width:100%;display:flex;flex-direction:column;margin:90px 0 0 0;justify-content:space-around}.cv-contact{display:flex;flex-direction:row;justify-content:space-around}.cv-contact-list{list-style:none;display:flex;flex-direction:row;justify-content:space-between;width:100%;max-width:500px}.cv-contact-list .icon{position:relative;top:6px}
.onepage{display:flex;justify-content:space-around}.onepage .container{max-width:1200px;border:dashed 2px;display:flex}.onepage .container h1{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace;font-size:1.5rem;font-weight:500;background-color:#fff;width:-webkit-max-content;width:-moz-max-content;width:max-content;position:absolute;margin-top:-20px;margin-left:15px}.onepage .container .sidepanel{width:20%;padding:10px;border-right:dashed 1px}.onepage .container .sidepanel h2{text-transform:uppercase;font-weight:300;margin-bottom:0}.onepage .container .sidepanel h4{margin-top:0;margin-bottom:0}.onepage .container .sidepanel h6{font-weight:600;font-size:1rem;margin-top:0;margin-bottom:0}.onepage .container .sidepanel .skills{display:flex;flex-direction:column}.onepage .container .sidepanel .solvertext{font-size:14px;padding-left:5px;text-align:justify}.onepage .container .sidepanel ul{margin-top:0;list-style:none;font-size:14px;padding-left:5px}.onepage .container .mainpanel{padding:10px}
